.feedback-form {
  position: relative;
  margin: 0;
  padding: 0;
  border-top: 1px solid $color-secondary-active;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 60px;
    border-top: 0 none;
  }

  @media (min-width: $desktop-width) {
    padding: 0 120px;
  }
}

.feedback-form__item {
  display: block;
  box-sizing: border-box;
  margin: 0;
  padding: 0 20px 0;
  border: 0;
  border-bottom: 1px solid $color-secondary-active;

  @media (min-width: $tablet-width) {
    width: 648px;
    padding: 0 20px;
    border: 0;
    border-top: 1px solid $color-secondary-active;
  }

  @media (min-width: $desktop-width) {
    width: 960px;
  }
}

@media (min-width: $tablet-width) {
  .feedback-form__item:nth-child(1),
  .feedback-form__item:nth-child(3) {
    width: 320px;
    border-top: 0;
  }
}

@media (min-width: $desktop-width) {
  .feedback-form__item:nth-child(1),
  .feedback-form__item:nth-child(3) {
    width: 480px;
    border-top: 0 none;
  }
}

@media (min-width: $tablet-width) {
  .feedback-form__item:nth-child(3) {
    order: -3;
    padding-right: 40px;
    border-right: 1px solid $color-secondary-active;
  }
}

@media (min-width: $tablet-width) {
  .feedback-form__item:nth-child(1) {
    padding-left: 55px;
    padding-bottom: 10px;
  }
}

.feedback-form__heading {
  padding: 20px;

  @include text-heading;
  color: $text-heading-color;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding-bottom: 55px;

    @include text-heading-tablet;
  }
}

@media (min-width: $tablet-width) {
  .feedback-form__item:nth-child(1) > .feedback-form__heading,
  .feedback-form__item:nth-child(3) > .feedback-form__heading {
    padding: 20px 0 30px;
    text-align: start;
  }

  @media (min-width: $desktop-width) {
    padding-left: 75px;
  }
}

.feedback-form__item--auxiliary {
  border-bottom: 0;
}

.feedback-form__comment {
  display: block;
  width: 100%;
  box-sizing: border-box;
  padding: 15px;

  @include text-heading;
  text-transform: none;
  border: none;
  outline: none;
  background: $color-secondary;

  @media (min-width: $tablet-width) {
    margin: 26px 0 0;
  }
}

.feedback-form__button-wrapp {
  margin: 50px 0;
  padding: 0;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 58px 0;
  }
}

.feedback-form__button {
  display: block;
  width: 280px;
  padding: 16px 0;
  margin: 0 auto;

  @include text-heading-tablet;
  color: $text-secondary-color;

  @media (min-width: $tablet-width) {
    width: 50%;
    margin: 0;
  }
}

.feedback-form__correction {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;

    @include text-label;
  }
}
